<template>
  <div :class="`whitespace whitespace-${size}`" :style="{backgroundColor: color}"></div>
</template>

<script>
export default {
  props: {
    size: {
      validate (value) {
        return ['1pixel', 'xs', 'sm', 'md', 'lg', 'xl'].indexOf(value) > -1
      },
      default: 'sm'
    },
    color: String,
    height: Number
  }
}
</script>

<style lang="scss" scoped>
  $height: retRem(6);
  $sizes: 1pixel xs sm md lg xl;
  $start: 0;

  .whitespace {
    @each $size in $sizes {
      &-#{$size} {
        height: $height * $start;
        @if $size == 1pixel {
          height: 1px;
          @media only screen and (-webkit-min-device-pixel-ratio: 2) {
            transform: scaleY(0.5);
            transform-origin: 50% 0;
          }
        }
      }
      $start: $start + 1;
      @if $size == 1pixel {
        height: 1px;
        @media only screen and (-webkit-min-device-pixel-ratio: 2) {
          transform: scaleY(0.5);
          transform-origin: 50% 0;
        }
      }
    }
  }
</style>
